.App {
  text-align: center;
  max-width: 1300px;
  max-height: 500px;
  display: inline-block;
  font-family: 'Amatic SC', cursive;
  position: fixed;
  margin: auto;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
}

h1 {
  font-size: 100px;
}

p {
  font-size: 50px;
}

.Question {
  padding-bottom: 50px;
  font-family: 'Amatic SC', cursive;
  font-size: 50px;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
